<template>
    <div class="van-top-bar" :class="[fixed ? 'van-top-bar_flexd' : '']" :style="[styleObject == null ? { 'backgroundColor': vmReversedColor} : styleObject]">
        <slot name="left" >
            <div class="van-top-bar_input van-top-bar_left">
                <input type="text" class="" placeholder="左边" />
            </div>
        </slot>
        <slot name="right" >
            <div class="van-top-bar_input van-top-bar_right">
                <input type="text" class="" placeholder="右边" />
            </div>
        </slot>
    </div>
</template>
<script>
    export default {
        props:{
            fixed:{
                type:Boolean,
                default:true
            },
            cc:{
                type:Boolean,
                default:true
            },
            backgroundColor:{
                type:String,
                default:"#e6ac01"
            }
        },
        data(){
            return {
                styleObject:null,
                scrolled:false,
                text:''
            }
        },
        methods:{
            colorToRgba(sColor){
                let reg =  /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;  
                sColor  = sColor.toLowerCase();
                if(sColor && reg.test(sColor)){
                    if(sColor.length === 4){
                        let symbol = '#';
                        for(var i=1; i<4; i+=1){  
                            symbol += sColor.slice(i,i+1).concat(sColor.slice(i,i+1));     
                        }  
                        sColor = symbol; 
                    }

                    //处理六位的颜色值
                    let sColorArray = [];
                    for(var i=1; i<7; i+=2){  
                        sColorArray.push(parseInt("0x"+sColor.slice(i,i+2)));    
                    }  
                    return sColorArray;
                }else{
                    return sColor;
                }
            },
            handleScroll(){
                const top = window.pageYOffset  //用于FF
                || document.documentElement.scrollTop
                || document.body.scrollTop
                || 0;
                const height = 92;
                const maxHeight = height + 10;
                if(top <= maxHeight){
                    let opacity = top / height;
                    opacity = opacity > 1 ? 1 : opacity;
                    //console.log('设置时间',(new Date).getTime());
                    let bgStyle =  "rgba(" + this.vmColor.join(",") + ","+opacity.toFixed(2)+")";
                    this.styleObject = {
                       'backgroundColor':bgStyle
                    }
                }else {
                    if(top > maxHeight){
                        //跳过设置最大值设置 反正数据重复更改
                        return;
                    }
                    this.styleObject = {
                       'backgroundColor':this.vmReversedColor
                    }
                }

            }

        },
        mounted(){
            //console.log(this.colorToRgba(this.backgroundColor));
        },
        computed:{
            vmColor : function(){
                return this.colorToRgba(this.backgroundColor);
            },
            vmReversedColor : function(){
                let Arr = this.colorToRgba(this.backgroundColor);
                return "rgba(" + Arr.join(",") + ",1)"; 
            },
            vmInitColor : function(){
                let Arr = this.colorToRgba(this.backgroundColor);
                return "rgba(" + Arr.join(",") + ",0)"; 
            }
        },
        activated(){
            window.addEventListener('scroll',this.handleScroll);
        },
        deactivated(){
            window.removeEventListener('scroll',this.handleScroll);
        }
    }
</script>
<style lang="less" scoped>
    .van-top-bar{
        height: 92px;
        -webkit-user-select: none;
        user-select: none;
        text-align: center;
        line-height: 92px;
        display: flex;
        padding: 0 24px;
        justify-content: space-between;
        align-items: center;
        // background: linear-gradient(to bottom,#fff2ca, #e6ac01);
        &_input{
            border: 0px;
            height: 44px;
            border-radius: 26px;
            padding: 4px 16px;
            font-size: 26px;
            line-height: 1;
            input{
               border: none;
               background: transparent;
               width: 100%;
               font-size: 26px;
               line-height: 44px;
            }
        }

        &_left{
           width: calc(70% - 80px);
           position: relative;
           background: rgba(0,0,0,.5);
           box-shadow: 0 0 10px rgba(0,0,0,.5);
           input{
               color: #fff;
               &::-moz-placeholder { color: #fff; }
               &::-webkit-input-placeholder { color:#fff; }
               &:-ms-input-placeholder { color:#fff; }
           }
        }
       
        &_right{
           width: calc(30% - 40px);
           color: #ddd;
           box-shadow: 0 0 10px #E8E8E8;
           background: #fff;
           input{
               color: #dddddd;
           }
        }
    }
    .van-top-bar_flexd{
        top: 0;
        left: 0;
        position: fixed;
        z-index: 1;
    }

</style>




