<script type="text/ecmascript-6">
import AsectRatio from "@/components/AspectRatio/AspectRatio";
import Swipe from "@/components/Swipe/Swipe";
import SwipeSkeleton from "@/components/Swipe/SwipeSkeleton";

import LabelTag from "@/components/LabelTag/LabelTag";

import CategoryMenu from "@/components/CategoryMenu/CategoryMenu";
import CategoryMenuSkeleton from "@/components/CategoryMenu/CategoryMenuSkeleton";

import LabelGroup from "@/components/LabelGroup/LabelGroup";

import IndexNavBar from "@/components/IndexNavBar/IndexNavBar";
import { component as VueLazyComponent } from "@xunlei/vue-lazy-component";

import { mapState , mapActions } from 'vuex';
export default {
  name: "index",
  components: {
    Swipe,
    SwipeSkeleton,
    VueLazyComponent,
    AsectRatio,
    LabelTag,
    CategoryMenu,
    CategoryMenuSkeleton,
    LabelGroup,
    IndexNavBar
  },
  data() {
    return {
      // list: [
      //   {
      //     link: "",
      //     imgUrl: "https://dummyimage.com/750x340/e6ac01/fff.png"
      //   },
      //   {
      //     link: "",
      //     imgUrl: "http://dummyimage.com/750x340/000/FFF.png"
      //   },
      //   {
      //     link: "",
      //     imgUrl: "http://dummyimage.com/750x340/fff/000.png"
      //   }
      // ],
      basicData: {
        menusGroup: [
          {
            text: "红酒",
            icon: "/static/img/index/icon1@2x.png",
            href: "/category?cid=1"
          },
          {
            text: "洋酒",
            icon: "/static/img/index/icon2@2x.png",
            href: "/category?cid=2"
          },
          {
            text: "白酒",
            icon: "/static/img/index/icon3@2x.png",
            href: "/category?cid=3"
          },
          {
            text: "啤酒",
            icon: "/static/img/index/icon4@2x.png",
            href: "/category?cid=4"
          },
          {
            text: "其他",
            icon: "/static/img/index/icon5@2x.png",
            href: "/category?cid=5"
          }
        ]
      }
    };
  },
  mounted(){
    //初始化数据
    this.initIndexData();
  },
  methods:{
    ...mapActions({
      initIndexData:'contextBasic/getIndexBasicData'
    })
  },
  computed:{
    ...mapState({
      age:(state) => state.userinfo.age,
      list:(state) => state.contextBasic.IndexBasicData.carousels
    })
  }
};
</script>
<style lang="stylus">
.van-category {
  &-head {
    background: #fff;
    display: flex;
    align-items: center;
    flex-flow: column;
    padding: 30px 0 10px 0px;
    border-bottom: 1px solid #f5f5f5;

    .van-category-title {
      font-size: 34px;
      font-weight: bold;
      color: #000;
      padding-bottom: 14px;
    }

    .van-category-prompt {
      font-size: 22px;
      color: #888;
    }
  }

  &-content {
    background: #fff;
    // border-bottom: 1px solid #f5f5f5;
    &_head{
      padding: 20px 24px 0px;
      border-bottom: 1px solid #f5f5f5;
      .bg {
        background: #f5f5f5;
        border-radius: 4px;
        width 702
        height 268px
        border-radius: 4px;
        img {
          width: 100%;
          border-radius: 4px;
          display: block;
        }
      }
    }
    .tips {
      font-size: 26px;
      padding: 26px 0px;
      color: #000;
      font-weight: bold;
    }

    &_list {
      position relative
      &:before{
        content ""
        position absolute
        top 0
        left 0
        right 0
        height 70px
        background #fff
      }

      &:after{
        content ""
        position absolute
        bottom 0
        left 0
        right 0
        height 30px
        background #fff
      }
      background-color: rgba(255,237,185,0.2);
      .van-category-container{
        padding:20px 24px 26px
        display flex
        overflow-y scroll
      }
      &_item{
        flex-shrink:0;
        flex-grow:1 
        width:220px;
        margin-right 10px;
        margin-bottom 24px
        border-radius: 8px;
        .item-head{
          position relative
          .product_img{
            width:100%;
            display block
            height:220px
          }
          .product_propmt{
            position:absolute;
            bottom:0;
            background-color:rgba(255,237,185,.8);
            font-size:22px;
            color:rgb(150,125,74)
            text-align center
            left 0
            right 0
          }
        }
        .item-body{
          display flex
          flex-flow column
          background #ffffff
          .product_name{
            padding-top 12px
            font-size 26px
            line-height 1.5
            color #000
            height 68px
            text-align center
          }
          .product_price{
            font-size 26px
            font-weight bold
            background #000
            color #e6ac01
            padding 4px 16px
            border-radius 4px
            width min-content
            margin 20px auto 0
          }
          .product_unprice{
            font-size 26px
            color #888
            padding 4px 8px
            border-radius 4px
            width min-content
            margin 10px auto 10px
            text-decoration: line-through;
          }
        }
      }
    }
  }
}
</style>



