<template>
    <div class="order-panel">
      <ul class="ui-flex txt-center">
        <li class="ui-flex-1" v-for="(item,key) in dataSouces" :key="key">
          <router-link :to="{path: item.href, query: {status: 0}}">
            <img :src="item.icon" class="ul-flex-iconImg" />
            <span>{{item.text}}</span>
          </router-link>
        </li>
      </ul>
    </div>
</template>
<script>
export default {
    props:{
        dataSouces:{
            type:Array
        }
    }
}
</script>
<style lang="stylus" scoped>
    .order-panel 
        padding 35px 48px
        background #fff
        .ui-flex 
            border-radius 4px
            display flex
            justify-content space-between
            .ui-flex-1
                a
                    display inline-block
                    .ul-flex-iconImg
                        width:54px
                        height:72px
                    span
                        font-size:26px
                        line-height:28px;
                        font-weight:bold
                        display: block
                        color: #000000
                  
</style>



