<template>
    <div class="label-group">
        <div class="label-group-item" v-for="(item,idx) in dataSouces" :key="idx">
            <div class="icon-item">
                <van-icon class="iconfont" class-prefix="icon" :name="item.iconclass"></van-icon>
            </div>
            <span>{{item.text}}</span>
        </div>
    </div>
</template>
<script>
export default {
    name:'label-group',
    props:{
        dataSouces:{
            type:Array,
            default:() =>[
                {iconclass:"zhengpinbaozheng",text:"正品保障"},
                {iconclass:"jiayipeishi",text:"假一赔十"},
                {iconclass:"ziying",text:"门店自营"},
                {iconclass:"kuaisupeisong1",text:"快速送达"}
            ]
        }
    }
}
</script>
<style lang="stylus" scoped>
    .label-group
        display flex
        text-align center
        padding 45px 0 45px
        background #ffffff
        .label-group-item 
            flex  1
            .icon-item
                margin-bottom 10px
                .iconfont 
                    width 40px 
                    height 40px
                    color #000000  
                    font-size 40px
            span
                font-size 26px
                color #000000 
</style>





