<template>
    <div class="aspectratio" :class="aspectClass" > 
        <div class="aspectratio-content">
            <slot></slot>
        </div>
    </div>
</template>
<script>
/**
 * 等宽比父组件,设置classname
 * @param aspectClass 
 * <asect-ratio aspectClass="classname">
 *     any html contents by insert
 * </asect-ratio>
 */
export default {
    props:['aspectClass']
}
</script>




