<template>
    <van-swipe :autoplay="3000" indicator-color="#eaeaea">
        <van-swipe-item v-for="(image, index) in list" :key="index">
            <a :href="image.link === '' ? 'javascript:;' : image.link">
                <img v-lazy="image.imgUrl" class="vant-swipe_img" />
            </a>
        </van-swipe-item>
    </van-swipe>
</template>
<script>
    export default {
        name:"dd-swipe",
        props:{
            list:Array,
            time:{
                type:Number,
                default:() => {
                    return 3000
                }
            }
        }
    }
</script>
<style lang="stylus">
    .van-swipe
        width 750px
        height 340px
        .van-swipe__track
            .vant-swipe_img
                width 100%
                display block
        .van-swipe__indicators
            .van-swipe__indicator
                width 40px
                height 6px
                background #f5f5f5
                border-radius: 6px
                opacity 0.8
                &--active
                    background #e6ac01 !important
                    opacity 1  
</style>





