<template>
    <div class="vant-temp-lab">
        <div class="vant-temp-lab-item" v-for="(item,key) in dataSouces" :key="key">
            <van-icon :name="item.icon" :color="item.styles" />
            <i class="solt-text">{{item.text}}</i>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        dataSouces:{
            type:Array,
            default: () => [
                {
                    icon:"passed",
                    text:"自营商品",
                    styles:"#fe3e3e",
                },
                {
                    icon:"passed",
                    text:"30分钟快速送达",
                    styles:"#fe3e3e",
                },
                {
                    icon:"passed",
                    text:"正品假一赔十",
                    styles:"#fe3e3e",
                }
            ]
        }
    }
}
</script>
<style lang="stylus" scoped>
    .vant-temp-lab
        display flex
        height 100%
        align-items center
        padding 0 30px
        justify-items center
        justify-content space-between
        background #ffffff
        &-item
            text-align center
            .van-icon
                font-size 24px
                
            .solt-text
                font-size 22px
                font-style inherit
                color #888
</style>



